.Landing-header {
  background-color: white;
  height: 150px;
  padding: 20px;
  color: #222;
}

.image-strip {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #FFF;
}

.landing-wrapper {
  display: -ms-flexbox;
  display: flex;
  padding: 1em;
  min-height: 400px;
}

.left {
  display: inline-block;
  width: 59%;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
}

.right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 39%;
}

.wrapper-item{
  padding: 2em;
  text-align: center;
}

.slick-slider {
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
}

.slick-list,
.slick-track {
  height: 100%;
}

.right button {
  width: 100%;
}

.left .wrapper-item .btn {
  background-color: #7ccea3;
  color: #ffffff;
  padding: 10px 10px;
}

.left .wrapper-item ul li i{
  color: #7ccea3;
}

@media screen and (max-width: 600px) {
  .left .right{
    display: block;
    width: 100%;
    text-align: center;
  }
  .wrapper-item{
    width: 100%;
    display: block;
  }

  .right {
    display: block;
    width: 100%;
  }
}



